body {
  background: #000;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.hidden {
  display: none;
}

.bg {
  height: 100vh;
  width: 100vw;
}

.bg > video {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  z-index: -1;
  z-index: -1;
  position: fixed;
  inset: 0;
}

#gjs-canvas {
  width: 100%;
  height: 100%;
  z-index: -1;
  margin: 0;
  display: none;
  position: absolute;
}

#output_canvas {
  z-index: -2;
  margin: 0;
  position: absolute;
}

.centered-container {
  width: 100%;
  height: 10%;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#start-text {
  word-spacing: .25em;
  text-align: center;
  color: #fff;
  z-index: 1;
  background: #000c;
  font-family: AmericanCaptain;
  font-size: 40px;
}

@font-face {
  font-family: AmericanCaptain;
  src: url("../american_captain.6bc61fac.ttf");
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #ee2027;
  border-radius: 50%;
  animation: 2s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

